
.App {
    text-align: center;
}
.App input, .App button{
    margin: 20px;
    padding: 10px 30px;
    border: 2px solid black;
    background-color: #fff;
    font-size: 20px;
   
    
}
.App button:hover{
    background-color:#000;
    color:red;
}
.result {
    border-top: 2px solid grey;
}

.result h3 {
    
    text-align: center;
    font-size: 50px;
}

.result h3 em{
    text-transform: uppercase;
    color:green;
}

.result h4 {
    text-align: center;
   margin: 80px 0px;
   font-size: 30px;

}

.result h4 em {
    color: royalblue;
}

